/**
 * oscar icon
 */
@keyframes bird-icon {
	0% { background-position: 0 0 }
	100% { background-position: -546px 0}
}

@keyframes dust-down-icon {
	0%{background-position: 0 0;}
	6% {background-position: -174px 0}
	50% {background-position: -174px 0}
	50.1% {background-position: -455px 0}
	100% {background-position: -455px 0}
}
@keyframes dust-up-icon {
	0%{background-position: 58px 0;}
	50% {background-position: 58px 0;}
	50.1% {background-position: 0 0;}
	56% {background-position: -174px 0}
	100% {background-position: -174px 0}
}

@keyframes swing-icon {
	0% { background-position: 0 0 }
	100% { background-position: -630px 0}
}
