.content-wrap .icon-dust {
  top: 188px;
  left: 41px;
  width: 58px;
  height: 124px;
}

.content-wrap .icon-dust:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 58px;
  height: 124px;
  animation: dust-down-icon 8s steps(3, end) infinite forwards 4s;
  background: url("/assets/images/sprite/club_house_dust_down1.png") no-repeat 0 0;
}

.content-wrap .icon-dust:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 58px;
  height: 124px;
  animation: dust-up-icon 8s steps(3, end) infinite forwards 4s;
  background: url("/assets/images/sprite/club_house_dust_up1.png") no-repeat 58px 0;
}

